* {
  margin: 0;
  padding: 0;
}

body,
html,
#root,
.App {
  min-height: 100%;
}

html {
  position: relative;
}

.bold {
  font-weight: bold;
}

.App {
  padding-top: 30px;
}

.content {
  width: 560px;
  margin: 0 auto;
  padding: 40px;
  margin-bottom: 80px;
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 20px;
  backdrop-filter: blur(10px);
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  text-align: center;
  font-size: 3em;
  color: whitesmoke;
}